body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'Digital-7';
    src: url(/Calculatrice/static/media/Digital-7.5d33d587.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}

.calculette{
    display: flex;
    width:430px;
    height: 650px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 80px 200px;
    border:3px solid black;
    border-radius: 30px;
    background-color: dimgray;
    box-shadow: 20px 20px 80px;
}

.ecran{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    border: 2px solid black;
    color: rgb(65, 65, 65);
    font-size: 3em;
    font-family: 'Digital-7';
    width: 360px;
    height: 400px;
    bottom: -50px;
    border-radius: 10px;
    box-shadow: inset 3px 3px 1px;
    background-color: #CCDECE;
}

.calcul{
    margin-bottom: 5px;
    margin-right: 10px;
}

.resultat{
    margin-bottom: 0px;
    margin-right: 10px;
}

.operateur{
    bottom: 320px;
    position: relative;
}

.boutons{
    width:320px;
    position: relative;
    display: block;
    bottom: -100px;
    right: 45px;
}

.null{
    position: relative;
    width: 80px;
    height: 48px;
    bottom: -10px;
    border: 2px solid black;
    border-radius: 10px;
    margin: 5px;
    font-size: 2em;
    font-weight: bold;
    background: -webkit-linear-gradient(top right,#d8d8d8 0%, #e6e6e6 100%);
    background: linear-gradient(to bottom left,#d8d8d8 0%, #e6e6e6 100%);
    box-shadow: 3px 3px 1px;
    outline: none;
    }


